* {
  box-sizing: border-box;
}

img {
  display: block;
  margin-right: 15px;
}

ul {
  padding: 0;
  list-style: none;
  font-size: 18px;
}

span {
  font-weight: 700;
  margin-right: 8px;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list li,
h1 {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
